.App {
  text-align: center;
}

body{
  color: whitesmoke;
  background-color: rgb(78, 78, 78);
}

.time-sets{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  /* grid-template-columns: 50% 20% 50%; */
  justify-items: center;
  align-items: center;
}

.container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 80%;
  margin: auto;
  justify-items: center;
  align-items: center;
}